<template>
  <div>
    <section class="p-tree">
      <div class="container">
        <div class="p-tree__body">
          <div class="p-tree__img img__bcg">
            <img src="../assets/images/customers/information.jpg" />
          </div>
          <div class="p-tree__list">
            <div class="p-tree__block"
              v-for="(product, key1, idx1) in productionList"
              :key="idx1"
            >
              <p class="p-tree__block__p">{{ product.name }}</p>
              <ul class="p-tree__block__ul">
                <li
                  v-for="(type, key2, idx2) in product.childrens"
                  :key="key2 + idx2"
                ><a :href="'/production/' + key1 + '/' + key2">{{ type.name }}<span
                  class="toggle-tree"
                  v-if="type.childrens"
                  @click.stop.prevent="toggleCildrenTree"
                ><i class="unshow">+</i><i class="show">-</i></span></a>
                  <ul class="p-tree__block__ul2">
                    <li
                      v-for="(item, key3, idx3) in type.childrens"
                      :key="key3 + idx3"
                    ><a :href="'/production/' + key1 + '/' + key2 + '/' + key3">{{ item.name }}</a></li>
                  </ul>
                </li>
              </ul>
            </div>
          </div>
          
        </div>
      </div>
    </section>

    <section class="p-cart">
      <div class="container">
        <div class="p-cart__body">
          <div class="p-cart__block"
            v-for="(product, key, idx) in productionList"
            :key="idx"
          >
            <div class="p-cart__block__img">
              <img :src="product.img" :alt="product.name" />
            </div>
            <p class="p-cart__block__p">{{ product.name }}</p>
            <a class="p-cart__link" :href="'/production/' + key" />
          </div>
        </div>
      </div>
    </section>
  </div>
</template>

<script>
import mixinProduct from '../product-mixin';

export default {
  name: 'ProductionMain',
  mixins: [mixinProduct],
  data() {
    return {
    };
  },
  methods: {
    toggleCildrenTree(e) {
      let elemSpan = e.target.closest('.toggle-tree');
      let elemLi = elemSpan.closest('li');
      
      elemSpan.classList.toggle('active');
      elemLi.classList.toggle('p-tree__block__li--active');
    }
  }
}
</script>

